/*contain css for our main react page, login page, sign up page and transaction history*/

@font-face {
    font-family: 'Mason Sans OT';
    src: url('/public/masonalternatebold.otf') format('opentype');
    /*font-family: 'Mason Sans OT', sans-serif;*/
}

html {
    cursor: url("/public/images/Harry Potter Cursor Normal Wand.png"), default;
}

.btn{
    cursor: url("/public/images/frame_15_delay-0.01s.png"), pointer;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'PT Sans', sans-serif;
    /*font-family: 'Mason Sans OT', sans-serif;*/
}
/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
    display: none;
    background-color: #323b6e;
}

body{

}

.login {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(180deg, #120B0E 0%, #323b6e 100%);
    min-height: 100vh;
    padding-bottom: 10%;
    overflow: hidden;
}

.login .logo {
    height: 200px;
    padding-bottom: 10px;
}

.login .flavor-text {
    color: #CDB373;
    text-align: center;
    font-family: Montaga;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 125% */
    padding-bottom: 20px;
}

.login .login-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    max-width: 210px;
    z-index: 2;
}

.login-form .fa-user {
    align-self: flex-start;
    position: absolute;
    margin: 7px 10px 0px 10px;
    color: #CDB373;
}

.login-form .fa-lock {
    align-self: flex-start;
    position: absolute;
    margin: 58px 10px 0px 10px;
    color: #CDB373;
}

.login button {
    width: 100%;
}

.login .error {
    color: red;
}

.login .image-box, .sign-up .image-box {
    width: 100vw;
}

.login .hogwart-art, .sign-up .hogwart-art {
    position: absolute;
    bottom: 5%;
    width: 100vw;
    object-fit: cover;
    /*z-index: 1;*/
}

.login .box, .sign-up .box {
    position: absolute;
    bottom: 0;
    width: 100vw;
    height: 5%;
    background-color: #18150D;
}

.login .login-form-auth h2 {
    margin-top: 30px;
    color: #CDB373;
    font-family: Montaga;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 50% */
    text-transform: capitalize;
}

.login .login-form-auth {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 210px;
    z-index: 2;
    gap: 30px;
}

.star-png {
    position: absolute;
    width: 110vw;
    height: 40vh;
    top: -100px;
    right: -70px;
    object-fit: cover;
}

.sign-up {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background: linear-gradient(180deg, #120B0E 0%, #323b6e 100%);
    min-height: 100vh;
    padding-bottom: 10%;
    overflow: hidden;
}

.sign-up .sign-up-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 90vh;
}

.sign-up .sign-up-form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 10px;
    z-index: 2;
}

.sign-up .form-col-fullName {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.sign-up .form-row {
    display: flex;
    flex-direction: row;
    gap: 50px;
}

.sign-up .form-col {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.sign-up button {
    width: 70%;
    margin: 20px 0px 0px 0px;
    display: flex;
    align-self: center;
    justify-content: center;
    z-index: 999;
}


.sign-up .logo-box {
    padding-right: 10%;
}

.sign-up .hogwart-art {
    position: absolute;
    bottom: 3%;
    width: 100vw;
    object-fit: cover;
    /*z-index: 1;*/
}

.sign-up .box {
    position: absolute;
    bottom: 0;
    width: 100vw;
    height: 3%;
    background-color: #18150D;
}

.sign-up .error-message {
    color: rgba(255, 0, 0, 0.68);
    font-size: small;
    align-self: flex-start;
    visibility: hidden;
}

.sign-up .error-message.visible {
    visibility: visible;
}

.dashboard {
    display: flex;
    background: #323b6e;
    min-height: 100vh;
}

.dashboard .left, .dashboard .middle, .dashboard .right {
    min-height: 100vh;
}

.dashboard .left {
    flex: 25%;
    box-shadow: 10px 0px 40px 0px rgba(0, 0, 0, 0.5);
    border-right: 2px solid #CDB373;
    border-bottom-right-radius: 30px;
    z-index: 0;
    position: relative;
    /* Style for navbar goes here */
}

@media screen and (max-width: 960px) {
    .dashboard .left{
        display: none;
    }
}

.dashboard .middle {
    flex: 50%;
    min-height: 100vh;
    box-shadow: 10px 0px 40px 0px rgba(0, 0, 0, 0.5);
    border-right: 2px solid #CDB373;
    border-bottom-right-radius: 30px;
    z-index: 4;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 50vw;
}

.dashboard .right {
    flex: 30%;
    min-height: 100vh;
    z-index: 3;
}

.middle h2, h3, h4 {
    color: white;
}

.middle h1 {
    color: #CDB373;
}

.middle .acc-balance-box {
    flex: 30%;
    width: 100%;
    padding: 30px;
    padding-bottom: 10px;
}

.acc-balance-box{
    display: flex;
    flex-wrap: wrap;
}

.acc-balance-box .balance-info-box {
    flex: 70%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.acc-balance-box .balance-buttons {
    flex: 30%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
}

.balance-buttons .button-container {
    flex: 1;
    display: flex;
    justify-content: right;
    align-items: end;
    width: 100%;
}

.middle .transac-history-box {
    flex: 70%;
    width: 100%;
    padding: 30px;
    padding-top: 0px;
    align-items: center;
    display: flex;
    flex-direction: column;
    max-height: 100%;
}

.transac-history-box h1{
    justify-self: left;
    width: 100%;
    padding-bottom: 30px;
    margin-top: 30px;
    color: #CDB373;
    font-family: 'Mason Sans OT', sans-serif;
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 50% */
    text-transform: capitalize;
}

.transac-history-box .filter-container {
    display: flex;
    flex-direction: row;
    align-self: flex-start;
    justify-content: left;
    align-items: flex-start;
    width: 100%;
    /*padding-bottom: 10px;*/
}

.transac-history-box .dropdown-list {
    width: 200px;  /* Adjust as needed */
    /*height: 72px;*/
    display: flex;
    align-items: center;
}

.transac-history-box .amount-inputs {
    flex: 1;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.transac-history-box .dropdown-list select{
    width: 100%;
    align-items: center;
    background-color: #323b6e;
    border: 1px solid #CDB373;
    color: white;
    font-size: 20px;
    border-radius: 10px;
    text-align: center;
    cursor: url("/public/images/frame_15_delay-0.01s.png"), pointer;

}

.transac-history-box .dropdown-list option {
    font-size: 15px;
    background-color: #424f94;
    cursor: url("/public/images/frame_15_delay-0.01s.png"), pointer;

}

.transac-history-box .input {
    font-size: 17px;
}

.filter-container > * {
    margin: 0px 10px 10px 10px;
}

.transac-history-box .transac-log-container {
    flex: 70%;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-height: 50vh;
    overflow: scroll;
    scrollbar-color: #CDB373 transparent;
    overflow-x: hidden;
}

.transac-log-container .filter-log {
    flex: 1;
    border: 2px;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.filter-log > .filter-name {
    flex-basis: 100%;
    color: #CDB373;
}

.filter-log .log-container {
    flex: 1;
    color: white;
    padding-left: 30px;
    display: flex;
    flex-direction: column;
}

.log-container .log {
    padding: 10px 0px 7px 0px;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #CDB373;
}

.log-container .log-box :hover{
    cursor: url("/public/images/frame_15_delay-0.01s.png"), pointer;

    /*background-color: rgba(37, 44, 81, 0.77);*/
    background-color: #004990;
    border-radius:10px;

}

.log-container .log :hover{
    cursor: url("/public/images/frame_15_delay-0.01s.png"), pointer;
}

.log .details {
    flex: 70%;
    margin-left:10px;
}

.log .details > * {
    padding-bottom: 10px;
}

.log .amount {
    flex: 20%;
}

